import { createElement, HTMLAttributes, PropsWithChildren } from 'react';
import c from 'classnames';

type SlideProps = PropsWithChildren<{
	tag: string;
}> &
	HTMLAttributes<HTMLElement>;

export default function Slide({ tag = 'li', children, className, ...props }: SlideProps) {
	return createElement(
		tag,
		{
			className: c('snap-slide', className),
			tabIndex: 0,
			...props
		},
		children
	);
}
