import { createElement, PropsWithChildren, useRef, HTMLAttributes } from 'react';
import c from 'classnames';

type CarouselProps = PropsWithChildren<{
	tag: string;
}> &
	HTMLAttributes<HTMLElement>;

export default function Carousel({ tag = 'ul', children, className }: CarouselProps) {
	// Refs
	const vsWrapperRef = useRef<HTMLElement>();

	return createElement(
		tag,
		{
			className: c('snap-carousel', className),
			ref: vsWrapperRef
		},
		children
	);
}
